@import '../../../scss/variable';

.search-wrapper {
    margin: 70px auto 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 50vh;

    @include mobile {
        margin: 30px auto 30px;
    }

    .search {
        // max-width: 768px;
        max-width: 980px;
        width: 100%;

        .search-header {
            font-size: 28px;
            font-weight: 700;
            text-align: center;
            margin-bottom: 24px;

            @include mobile {
                font-size: 22px;
            }

            .search-title-key {
                font-size: 26px;
                font-weight: 700;
                font-style: italic;
                color: $primary-color;

                @include mobile {
                    font-size: 23px;
                }
            }
        }

        .search-body {
            box-shadow: 0 10px 15px -3px rgba(222, 36, 84, .07), 0 4px 6px -2px rgba(222, 36, 84, .025);
            border: 1px solid #e2e8f0;
            padding: 24px 36px 24px 36px;
            border-radius: 5px;

            @include mobile {
                padding: 12px 18px 12px 18px;
            }

            .search-body-top {
                display: flex;
                justify-content: space-evenly;
                padding-bottom: 4px;
                border-bottom: 1px solid #e2e8f0;

                @include mobile {
                    padding-bottom: 3px;
                }

                .search-body-top-item-wrapper {
                    width: 100%;

                    .search-body-top-item {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 100%;
                        width: 100%;
                        gap: 6px;
                        padding-bottom: 8px;
                        cursor: pointer;

                        span {
                            line-height: 1;
                            font-size: 20px;
                            font-weight: 700;

                            @include mobile {
                                font-size: 18px;
                            }

                            i {
                                line-height: 1;
                                font-size: 24px;
                                font-weight: 700;

                                @include mobile {
                                    font-size: 20px;
                                }
                            }
                        }

                        .active {
                            color: $primary-color;
                        }
                    }

                    .search-body-top-item-border.active {
                        width: 100%;
                        height: 0px;
                        border-bottom: 4px solid #fff;
                    }

                    .search-body-top-item-border.active {
                        width: 100%;
                        height: 0px;
                        border-bottom: 4px solid $primary-color;
                    }


                }
            }

            .search-body-mid {}

            .search-body-bottom {}
        }
    }
}